.playersContainer{
		width:100%;
		position: absolute;
		top:20px;
		right:20px;
		bottom:20px;
		left:20px;
		 // height:100vh; 
		// background:white;
		.noPlayers{
			font-size:30px;
			text-align: center;
			width:100%;
			color:#000;
			opacity: 0.25;
			padding:40px 20px;
		}
		.fakeMenu{
			display: flex;
			flex-direction: row;
			align-items: center;
			margin-bottom:10px;
			.fakeMenuItem{
				font-weight: extra-light!important;
				color:#000;
				margin:10px;
				font-size:25px;
				opacity:0.5;
			}
			.gridIcon{
				opacity:0.5;
				margin-left:10px;
				width:30px;
				height:30px;
				display: flex;
				flex-direction: row;
				flex-wrap: wrap;
				.gridIconSquare{
					width:5px; 
					height:5px;
					background:#333;
					margin:2px;
				}
			}
			.profileCircle{
				width:30px;
				height:30px;
				margin-left:20px;
				background:#0F9D58;
				border-radius: 50%;
				display: flex;
				align-items: center;
				justify-content: center;
				color:white;
				font-size: 15px;
			}
		}
		.playersInnerChevron{
			position: absolute;
			top:-10px;
			right:55px;;
			width: 0; 
		  height: 0; 
		  border-left: 10px solid transparent;
		  border-right: 10px solid transparent;
		  border-bottom: 10px solid white;
		}
		.playersModalBackground{
			border-radius:4px;
			background:white;
			position: absolute;
			top:70px;
			left:0px;
			// bottom:0px;
			right:40px;
			border-radius:5px;
			box-shadow: 0px 0px 20px rgba(0,0,0,0.3);
			opacity:0;
			// overflow:hidden;

			&.isVisible{
				transition:all 0.3s ease;
				height:0;

				opacity:1;
			}
			.modalInfo{
				color:#000;
				opacity:0.25;
				text-align: center;
				font-size:20px;
				width:100%;
			}
			.playersInner{
				border-radius:5px;
				box-shadow: 0px 0px 20px rgba(0,0,0,0.3);
				background:white;
				padding:10px 0;
				justify-content: flex-start;
				display: flex;
				flex-direction: row;
				flex-wrap:wrap;
			}
			
		}
		.filler{
			flex:1;
			width:100%;
			background:red;
			position: absolute;

		}
		
	}